* {
  box-sizing: border-box; }

body,
html {
  font-family: 'Arial';
  font-size: 16px;
  box-sizing: border-box; }

*:focus {
  outline: none; }

.drop-shadow {
  box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.12), inset 0 0 0 rgba(0, 0, 0, 0.24), 0 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3); }

.inner-shadow {
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.12), inset 0 1px 2px rgba(0, 0, 0, 0.24), 0 0px 0px rgba(102, 119, 136, 0.03), 0 0px 0px rgba(102, 119, 136, 0.3); }

.border-radius {
  border-radius: 15px; }

.border-gray {
  border: 1px solid #cacaca; }

.detail {
  font-size: 12px; }

.Controls {
  height: 40px; }

.Button {
  border-radius: 15px;
  padding-right: 40px;
  padding-left: 40px;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 1px;
  border: none;
  transition: all 0.04s ease-in-out;
  cursor: pointer; }

.Keyboard {
  height: 143px; }

.Key {
  position: relative;
  height: 50px;
  width: 30px;
  transition: all 0.07s ease-in-out;
  cursor: pointer; }
  .Key--large {
    height: 70px;
    width: 40px;
    border-radius: 20px; }
  .Key--sharp {
    position: absolute; }
  .Key--regular {
    position: relative;
    top: 73px; }

.Input {
  width: 200px;
  border-radius: 15px;
  font-size: 20px;
  padding-left: 25px;
  padding-right: 15px;
  border: none; }
  .Input--small {
    width: 150px; }

.Select {
  width: 220px;
  border-radius: 15px;
  font-size: 14px;
  padding-left: 25px;
  padding-right: 15px;
  border: none;
  background-color: #ffffff;
  appearance: none;
  cursor: pointer;
  transition: color 0.08s ease-in-out; }
  .Select--small {
    width: 150px; }
  .Select:hover {
    color: #cacaca; }

.StatusIndicator__Container {
  border: 1px solid #cacaca;
  width: 15px;
  height: 15px;
  border-radius: 8px;
  padding: 2px; }

@keyframes statusBlink {
  from {
    background-color: #f1f3f5; }
  to {
    background-color: #0600ff; } }

.StatusIndicator {
  background-color: #cacaca;
  height: 100%;
  width: 100%;
  border-radius: 4px; }
  .StatusIndicator--active {
    animation-name: statusBlink;
    animation-duration: 0.65s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: cubic-bezier(0.27, 0.76, 0.54, 0.91); }
